:root {
  --bg-color: #7ebaf1;
  --text-color: #333;
  --sidebar-bg: #61c7f0;
  --sidebar-active: #81f5e1;
  --card-bg: #fff;
  --modal-bg: #3df5fc;
  --form-bg: #fff;
  --input-bg: #fff;
  --btn-primary: #007bff;
  --btn-hover: #0056b3;
}

body.dark {
  --bg-color: #1e1e1e;
  --text-color: #fff;
  --sidebar-bg: #2c3e50;
  --sidebar-active: #3498db;
  --card-bg: #2c2c2c;
  --modal-bg: #34495e;
  --form-bg: #2c2c2c;
  --input-bg: #3c3c3c;
  --btn-primary: #3498db;
  --btn-hover: #2980b9;
}

* {
  box-sizing: border-box;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  margin: 0;
  font-family: Roboto, sans-serif;
  transition: background-color .3s, color .3s;
}

::selection {
  color: #fff;
  background-color: #a7d9ed;
}

.app-container {
  min-height: 100vh;
  display: flex;
  overflow: hidden;
}

.sidebar {
  background-color: var(--sidebar-bg);
  width: 240px;
  padding: 20px;
  box-shadow: 2px 0 5px #0000001a;
}

.logo {
  color: #fff;
  margin-bottom: 20px;
  font-size: 1.6em;
  font-weight: bold;
}

.sidebar ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.sidebar li {
  cursor: pointer;
  padding: 10px;
  transition: background-color .2s;
}

.sidebar li:hover {
  background-color: #6dd6f8;
  border-radius: 20px;
}

.sidebar li.active {
  background-color: var(--sidebar-active);
  color: #000;
  border-radius: 20px;
  font-weight: bold;
}

.main-content {
  flex-grow: 1;
  padding: 20px;
  overflow-y: auto;
}

.header {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.search-wrapper {
  align-items: center;
  gap: 8px;
  display: flex;
}

.search-bar {
  background-color: var(--input-bg);
  width: 100%;
  max-width: 300px;
  color: var(--text-color);
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 8px;
}

.header-icons {
  align-items: center;
  gap: 15px;
  display: flex;
}

.profile {
  font-weight: bold;
}

.dashboard {
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 20px;
  display: flex;
}

.card {
  background-color: var(--card-bg);
  border-radius: 8px;
  flex: 1;
  min-width: 200px;
  padding: 15px;
  box-shadow: 0 2px 5px #0000001a;
}

#content-area h2 {
  color: var(--btn-primary);
  margin-bottom: 15px;
  font-size: 1.6rem;
}

.table-actions {
  border-radius: 8px;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 10px;
  margin-bottom: 15px;
  display: flex;
}

.table-actions button {
  background-color: var(--btn-primary);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  padding: 8px 14px;
  font-size: 14px;
  transition: background-color .3s;
}

.table-actions button:hover {
  background-color: var(--btn-hover);
}

.table-wrapper {
  background-color: var(--card-bg);
  border-radius: 8px;
  width: 100%;
  margin-bottom: 20px;
  overflow-x: auto;
  box-shadow: 0 2px 6px #0000001a;
}

table {
  border-collapse: collapse;
  background-color: var(--card-bg);
  width: 100%;
  min-width: 700px;
}

th, td {
  text-align: left;
  color: var(--text-color);
  border-bottom: 1px solid #ccc;
  padding: 10px 12px;
  font-size: 18px;
}

th {
  font-weight: 600;
}

td {
  vertical-align: middle;
}

td:last-child {
  white-space: nowrap;
}

td:last-child button {
  cursor: pointer;
  color: #fff;
  background-color: #2196f3;
  border: none;
  border-radius: 4px;
  margin-right: 6px;
  padding: 6px 10px;
  font-size: 13px;
  transition: background-color .2s;
}

td:last-child button:hover {
  background-color: #1976d2;
}

.modal {
  z-index: 1000;
  opacity: 1;
  visibility: visible;
  background: #00000080;
  justify-content: center;
  align-items: center;
  transition: opacity .3s, visibility .3s;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal.hidden {
  opacity: 0;
  visibility: hidden;
}

.modal-content {
  background: var(--modal-bg);
  min-width: 300px;
  max-width: 90vw;
  color: var(--text-color);
  border-radius: 8px;
  padding: 2rem;
  position: relative;
}

.modal-content.start-animation {
  animation: .3s fadeIn;
}

.modal-close {
  cursor: pointer;
  font-size: 1.5rem;
  position: absolute;
  top: 10px;
  right: 15px;
}

.form-container {
  background-color: var(--form-bg);
  border-radius: 10px;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

.form-container h2 {
  text-align: center;
  color: var(--btn-primary);
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  color: var(--text-color);
  margin-bottom: 5px;
  font-weight: 500;
  display: block;
}

.form-group input, .form-group select, .form-group textarea {
  background-color: var(--input-bg);
  width: 100%;
  color: var(--text-color);
  border: 1px solid #000;
  border-radius: 4px;
  padding: 6px 8px;
  font-size: 13px;
}

.form-group textarea {
  resize: vertical;
}

.btn {
  cursor: pointer;
  border: none;
  border-radius: 6px;
  padding: 10px 15px;
  font-size: 16px;
  transition: background-color .3s;
}

.btn-primary {
  background-color: var(--btn-primary);
  color: #fff;
}

.btn-primary:hover {
  background-color: var(--btn-hover);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(.95);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@media (width <= 768px) {
  .app-container {
    flex-direction: column;
  }

  .sidebar {
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
    display: flex;
  }

  .main-content {
    padding: 10px;
  }

  .dashboard {
    flex-direction: column;
  }

  .search-bar {
    width: 100%;
  }

  .table-actions {
    justify-content: center;
  }

  .table-actions button {
    width: 100%;
    max-width: 200px;
  }
}

#reporteHeader {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  display: flex;
}

#reporteHeader h3 {
  margin: 0;
}

.btn-secondary {
  color: #fff;
  background-color: #6c757d;
}

.btn-secondary:hover {
  background-color: #5a6268;
}

.chart-container {
  width: 100%;
  max-width: 800px;
  height: 400px;
  margin: auto;
  position: relative;
}
